	import appOrderTime from '../components/app-order-time.vue';
	
    export default {
        name: 'app-order-list',
	    props: {
            list: {
                type: Array,
	            default() {
                    return [];
	            }
            },
			theme: Object
	    },
	    methods: {
            goPay(order_id, id) {
                let _this = this;
                uni.showModal({
                    title: '提示',
                    content: '订单支付',
                    cancelText: '取消',
                    confirmText: '确定',
	                success(res) {
                        if (res.confirm) {
                            _this.$request({
	                            url: _this.$api.order.list_pay_data,
	                            data: {
	                                id: order_id,
	                            }
                            }).then(response => {
                                if (response.code === 0) {
                                   _this.$payment.pay(response.data.id).then(() => {
                                       for (let i = 0; i < _this.list.length; i++) {
                                           if(_this.list[i].id === id){
                                               _this.$emit('click', i);
                                           }
                                       }
                                   }).catch(() => {
                                   })
                                }
                            })
                        }
	                }
                })
            }
	    },
	    components: {
            'app-order-time': appOrderTime,
	    }
    }
